import { gql } from '@apollo/client';

export const LOGIN = gql`
  mutation login($email: String!, $password: String!) {
    login(email: $email, password: $password) {
      token
      user {
        _id
      }
    }
  }
`;

export const ADD_USER = gql`
  mutation addUser(
    $firstName: String!
    $lastName: String!
    $email: String!
    $password: String!
  ) {
    addUser(
      firstName: $firstName
      lastName: $lastName
      email: $email
      password: $password
    ) {
      token
      user {
        _id
      }
    }
  }
`;

export const UPDATE_BID = gql`
mutation updateBid($_id: ID!, $currentBid: Float!) {
  updateBid(_id: $_id, currentBid: $currentBid){
    currentBid
    _id
   }
  }
`
export const SAVE_FLIGHT = gql`
  mutation saveflight($_id: ID!) {
       saveflight(winingAuctions: $_id) {
        winingAuctions{
            _id
        }
      }
    }
`;

export const SAVE_TO_WATCHLIST = gql`
  mutation saveToWatchlist($_id: ID!) {
    saveToWatchlist(watchAuctions: $_id) {
      watchlistAuctions{
          _id
        }
      }
    }
`;

export const DELETE_FROM_WATCHLIST = gql`
  mutation deleteFromWatchlist($_id: ID!) {
    deleteFromWatchlist(_id: $_id) {
      firstName
      watchlistAuctions {
        _id
    }
  }
}
`;

export const UPDATE_LATESTBID_USER = gql`
  mutation updateLatestBidUser($_id: ID!) {
    updateLatestBidUser(auctions: $_id) {
      latestBidUser{
        _id
      }
    }
  }
`;

export const DELETE_FLIGHT = gql`
  mutation deleteflight($auctionId: ID!, $remuserId: ID!) {
    deleteflight(auctionId: $auctionId, remuserId: $remuserId) {
      winingAuctions{
          _id
        }
      }
    }
`;

export const UPDATE_BID_HISTORY = gql`
  mutation updateBidHistory($auctionId: ID!, $bidAmount: Float!) {
    updateBidHistory(auctionId: $auctionId, bidAmount: $bidAmount) {
      currentBid
      }
    }
`;

export const ADD_ORDER = gql`
  mutation addOrder($products: [ID]!) {
    addOrder(products: $products) {
      purchaseDate
      products {
        _id
        name
        description
        price
        quantity
        category {
          name
        }
      }
    }
  }
`;